<template>
  <div class="chat">
    <!--  -->
    <div class="talk_input">
      <!-- : selected,valueselect -->
      <select class="whotalk" id="who" v-model="str1">
        <option value="0">A</option>
        <option value="1">B</option>
      </select>
      <!--  -->
      <input type="text" class="talk_word" id="talkwords" v-model="str2" />
      <!--  -->
      <input type="button" value="" class="talk_sub" id="talksub" @click="add_data()" />
    </div>
    <div class="talk_con2">
      <!--  -->
      <div class="talk_show" id="words" ref="chattingContent">
        <!-- vue -->
        <div v-for="(i,index) in arr" :class="i.person=='A'?'atalk':'btalk'" :key="index">
          <span>{{ i.person }}{{ i.words }}</span>
        </div>
      </div>

      <!--  -->
      <div class="talk_input">
        <!-- : selected,valueselect -->
        <select class="whotalk" id="who" v-model="str1">
          <option value="0">A</option>
          <option value="1">B</option>
        </select>
        <!--  -->
        <input type="text" class="talk_word" id="talkwords" v-model="str2" @keyup.enter="add_data()" />
        <!--  -->
        <input type="button" value="" class="talk_sub" id="talksub" @click="add_data()" />
      </div>
    </div>
  </div>
</template>

<script>


export default {
  props: {},
  data() {
    return {
      arr: [
        { person: "A", words: "" },
        { person: "B", words: "" }
      ],
      str1: "0",
      str2: ""
    };
  },
  methods: {
    add_data: function() {
      // bug: 
      if (this.str2 == "") {
        alert("...");
        return;
      }

      // alert(111)
      // : (),
      // 
      //     : :  1.   2.(/)
      //         ():    [{}, {}, {person:'A/B', words:'...'}]

      // 
      // alert(this.str1) // 0/1
      // alert(this.str2)
      var p = this.str1 == "0" ? "A" : "B";
      var obj = { person: p, words: this.str2 };
      this.arr.push(obj);

      // 
      this.str2 = "";
      setTimeout(() => {
          this.$refs.chattingContent.scrollTop = this.$refs.chattingContent.scrollHeight;
    console.log(this.$refs.chattingContent.scrollTop)
        }, 0);
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.chat{
    font-size: .2rem;
}
.talk_con1 {
  width: 6rem;
  height: 5rme;
  border: 0.01rem solid #666;
  margin: 0.3rem;
  float: left;
  background: #e98080;
}

.talk_con2 {
  width: 6rem;
  height: 5rem;
  border: 0.01rem solid #666;
  margin: 0.3rem;
  float: right;
  background: #7adbd3;
}
.talk_show {
  width:100%;
  height: 10.2rem;
  border: 0.01 solid #666;
  background: #fff;
  margin: 10px auto 0;
  overflow: auto;
}
.talk_input {
  width: 5.8rem;
  margin: 0.1rem auto 0;
  position: fixed;
}
.whotalk {
  width: 0.8rem;
  height: 0.3rem;
  float: left;
  outline: none;
}
.talk_word {
  width: 4.2rem;
  height: 0.26rem;
  padding: 0rem;
  float: left;
  margin-left: 0.1rem;
  outline: none;
  text-indent: 0.1rem;
}
.talk_sub {
  width: 0.56rem;
  height: 0.3rem;
  float: left;
  margin-left: 0.1rem;
}
.atalk {
  margin: 0.1rem;
}
.atalk span {
  display: inline-block;
  background: #0181cc;
  border-radius: 0.1rem;
  color: #fff;
  padding: 0.05rem 0.1rem;
}
.btalk {
  margin: 0.1rem;
  text-align: right;
}
.btalk span {
  display: inline-block;
  background: #ef8201;
  border-radius: 0.1rem;
  color: #fff;
  padding: 0.05rem 0.1rem;
}
</style>



